.TextInput {
  width: 100%;
  height: 42px;
  background-color: #fff !important;
  border: none;
  outline: none;
  margin: 10px 0px !important;
  padding: 0px;
  border-radius: 4px;
  padding-left: 10px;
  font-size: 14px;
  font-weight: 700;
  font-family: "Arial";
  color: #6c7075;
  border: 1px solid #d4d7d9;
  .MuiInputBase-root {
    height: 100%;
    border-radius: 4px;
    overflow: hidden;
    .MuiInputBase-input {
      padding-left: 10px;
      font-weight: 700;
      font-size: 14px;
      font-family: "Arial";
      color: #6c7075;
    }
  }
}
input.TextInput::-webkit-input-placeholder {
  font-weight: 400;
}
input.MuiInputBase-input::-webkit-input-placeholder {
  font-weight: 400;
  color: #6c7075;
  font-size: 14px;
  opacity: 1;
  font-family: "Arial";
}
input.MuiInput-input::-webkit-input-placeholder {
  font-weight: 400;
  color: #6c7075;
  font-size: 14px;
  opacity: 1;
  font-family: "Arial";
}
input.MuiInputBase-inputAdornedEnd::-webkit-input-placeholder {
  font-weight: 400;
  color: #6c7075;
  font-size: 14px;
  opacity: 1;
  font-family: "Arial";
}
.error {
  font-family: "Montserrat";
  font-size: 12px !important;
  font-weight: 400;
  text-align: left;
  color: red;
}
.MuiFormHelperText-root {
  display: none !important;
}
.MuiInput-underline.Mui-error:after {
  border-bottom-color: #fff !important;
}
* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}
